
.button-link {
  color: #fff;
  text-transform: uppercase;
  padding: 10px 0;
  background-color: $blue-accent;
  border: 1px solid $blue-accent;
  font-weight: 700;
  font-size: 14px;
  border-radius: 5px;
  text-align: center;
}

.button-link:hover {
  background-color: #fff;
  color: $blue-accent;
  border: 1px solid $blue-accent;
  transition: background-color 0.3s ease-in-out;
}
